.resizer {
  position: relative;
  top: -9px;
  background-color: #222;
  margin-left: auto;
  margin-right: auto;
  width: 40px;
  height: 16px;
  cursor: row-resize;
  border: 2px solid #444;
  border-radius: 5px;
  z-index: 3;

}

.chartcontainer{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}

.chart-wrapper{
  margin: auto;
}

.ant-layout {
  height: 100vh;
  max-height: 100%;
  overflow: hidden;
  font-family: 'Source Sans Pro', sans-serif;
}

